import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { ApiService } from './api.service';

export type ParameterType = "string" | "number" | "date";

export interface ParameterConfig {
  type: ParameterType,
  default?: boolean
}

export interface Parameter {
  [x: string]: ParameterConfig | ParameterType
}

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  
  private _subject = new Subject<any>();

  public job: Parameter = {
    name: {
      type: "string",
      default: true
    },
    created_at: {
      type: "date",
      default: true
    }
  }

  constructor(private api: ApiService) {
    this.api.getJobFields().subscribe(res => {
      if (res) {
        this.job = res;
      }
    })
  }

  addJobFields(o: any): void {
    this.job = {...this.job, ...o};
    this.api.addJobFields(this.job);
  }

  removeJobField(o: any): void {
    delete this.job[o.name];
    this.api.removeJobField(this.job);
  }

  newEvent(event) {
    this._subject.next(event);
  }

  get events$ () {
    return this._subject.asObservable();
  }
}
