import { Component, OnInit } from "@angular/core";
import { MatDialog, MatTableDataSource } from "@angular/material";
import Swal, { SweetAlertType } from "sweetalert2";
import { SharedService } from "../shared.service";
import { AddPropertyComponent } from "./add-property/add-property.component";

@Component({
  selector: "app-user",
  templateUrl: "./user.component.html",
  styleUrls: ["./user.component.scss"]
})
export class UserComponent implements OnInit {
  ELEMENT_DATA: any[] = [];
  displayedColumns: string[] = ["name", "type", "action"];
  dataSource = new MatTableDataSource(this.ELEMENT_DATA);

  get user(): string {
    return localStorage.getItem("user");
  }

  get pass(): string {
    return localStorage.getItem("pass");
  }

  constructor(private service: SharedService, public dialog: MatDialog) {
    this.load();
  }

  ngOnInit() {}

  addNewParameter(): void {
    const dialogRef = this.dialog.open(AddPropertyComponent, {
      width: "350px",
      data: { name: "", type: "string" }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!result) {
        return;
      }
      let o = {};
      o[result.name] = result.type;
      this.service.addJobFields(o);
      this.load();
    });
  }

  delete(e): void {
    const type: SweetAlertType = "warning";
    const options = {
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this property!",
      type: type,
      showCancelButton: true
    };
    Swal.fire(options).then(willDelete => {
      if (willDelete.value) {
        this.service.removeJobField(e);
        this.load();
      }
    });
  }

  private load(): void {
    this.ELEMENT_DATA = [
      {
        name: "Service 1",
        type: "Some info "
      }
    ];
    this.dataSource = new MatTableDataSource(this.ELEMENT_DATA);
  }
}
