import { Component, OnDestroy, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { ApiService } from "../api.service";
import { SharedService } from "../shared.service";

@Component({
  selector: "app-new",
  templateUrl: "./new.component.html",
  styleUrls: ["./new.component.scss"]
})
export class NewComponent implements OnInit, OnDestroy {
  ELEMENT_DATA: any[] = [];
  displayedColumns: string[] = ["no"];
  iterableDisplayedColumns: string[] = [""];
  dataSource;
  private unsub: Subject<any> = new Subject();

  constructor(private service: SharedService, private api: ApiService) {
    this.load();

    let fields: string[] = Object.keys(this.service.job);
    fields.push(fields.splice(fields.findIndex(v => v == "created_at"), 1)[0]);

    this.displayedColumns = this.displayedColumns.concat(fields);
    this.iterableDisplayedColumns = this.iterableDisplayedColumns.concat(
      fields
    );
  }

  ngOnInit() {
    this.service.events$.pipe(takeUntil(this.unsub)).subscribe(res => {
      this.api.createOffer(res).subscribe(r => {
        this.load();
      });
    });

    this.api
      .createOffer({
        name: "blae",
        companyId: 22
      })
      .subscribe(r => {
        this.load();
      });
  }

  ngOnDestroy(): void {
    this.unsub.next();
    this.unsub.complete();
  }

  private load(): void {
    this.api.getOffers().subscribe(r => {
      this.ELEMENT_DATA = r;
      this.dataSource = new MatTableDataSource(r);
    });
  }
}
