import { AgmCoreModule } from "@agm/core";
import { MatGoogleMapsAutocompleteModule } from "@angular-material-extensions/google-maps-autocomplete";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import {
  GestureConfig,
  MatAutocompleteModule,
  MatButtonModule,
  MatCheckboxModule,
  MatDatepickerModule,
  MatDialogModule,
  MatIconModule,
  MatInputModule,
  MatMenuModule,
  MatNativeDateModule,
  MatProgressSpinnerModule,
  MatSelectModule,
  MatSliderModule,
  MatSnackBarModule,
  MatTableModule,
  MatTabsModule,
  MatTooltipModule
} from "@angular/material";
import {
  BrowserModule,
  HAMMER_GESTURE_CONFIG
} from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { CalendarModule, DateAdapter } from "angular-calendar";
import { adapterFactory } from "angular-calendar/date-adapters/date-fns";
import { OAuthModule } from "angular-oauth2-oidc";
import { AddNewComponent } from "./add-new/add-new.component";
import { AppRoutingModule } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { CalendarComponent } from "./calendar/calendar.component";
import { DefaultOAuthInterceptor } from "./interceptor";
import { NewComponent } from "./new/new.component";
import { RegisterComponent } from "./register/register.component";
import { SearchComponent } from "./search/search.component";
import { SecureLayoutComponent } from "./secure-layout/secure-layout.component";
import { ServicesComponent } from "./services/services.component";
import { SharedService } from "./shared.service";
import { TodayComponent } from "./today/today.component";
import { AddPropertyComponent } from "./user/add-property/add-property.component";
import { UserComponent } from "./user/user.component";
import { PreviewComponent } from "./view/preview/preview.component";
import { SendComponent } from "./view/send/send.component";
import { ViewComponent } from "./view/view.component";

const googleMapsParams = {
  apiKey: "AIzaSyAFdkp7b5VrPPHGUtAQPmygp5vBHZz5BDw",
  libraries: ["places"],
  language: "en"
  // region: 'DE'
};

@NgModule({
  declarations: [
    AppComponent,
    NewComponent,
    TodayComponent,
    CalendarComponent,
    AddNewComponent,
    AddPropertyComponent,
    ViewComponent,
    UserComponent,
    SendComponent,
    PreviewComponent,
    RegisterComponent,
    ServicesComponent,
    SecureLayoutComponent,
    SearchComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatTableModule,
    MatIconModule,
    MatInputModule,
    MatSnackBarModule,
    MatMenuModule,
    MatProgressSpinnerModule,
    MatSelectModule,
    MatTabsModule,
    MatAutocompleteModule,
    MatTooltipModule,
    MatDialogModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatSliderModule,
    ReactiveFormsModule,
    CalendarModule.forRoot({
      provide: DateAdapter,
      useFactory: adapterFactory
    }),
    OAuthModule.forRoot({
      resourceServer: {
        allowedUrls: ["https://projectx-api.azurewebsites.net"],
        sendAccessToken: true
      }
    }),
    AgmCoreModule.forRoot(googleMapsParams),
    MatGoogleMapsAutocompleteModule.forRoot()
  ],
  providers: [
    SharedService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: DefaultOAuthInterceptor,
      multi: true
    },
    { provide: HAMMER_GESTURE_CONFIG, useClass: GestureConfig }
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    AddNewComponent,
    AddPropertyComponent,
    SendComponent,
    PreviewComponent
  ]
})
export class AppModule {}
