import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/internal/Observable";
import { of } from "rxjs/internal/observable/of";
import { Offer, OfferDetail } from "./shared/job.model";
import { offers } from "./shared/test-data";

export interface OfferItem {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}

const ELEMENT_DATA: OfferItem[] = [
  { position: 1, name: "Željzo štange 2m", weight: 1.0079, symbol: "H" },
  { position: 2, name: "Šarafi", weight: 4.0026, symbol: "He" },
  { position: 3, name: "Ulje", weight: 6.941, symbol: "Li" },
  { position: 4, name: "Materijali", weight: 9.0122, symbol: "Be" },
  { position: 10, name: "Alati", weight: 20.1797, symbol: "Ne" }
];

@Injectable({
  providedIn: "root"
})
export class ApiService {
  private url = "https://projectx-api.azurewebsites.net";

  constructor(private http: HttpClient) {}

  getOffers(): Observable<Offer[]> {
    return of(offers.filter(i => !i.parent_id));
  }

  getOfferDetail(id: number): Observable<OfferDetail> {
    const offer = offers.find(i => i.id === id);

    if (!offer) {
      return of(null);
    }

    if (offer && offer["items"]) {
      return of((offer as unknown) as OfferDetail);
    }

    const o: OfferDetail = {
      id: offer.id,
      created_at: offer.created_at,
      name: offer.name,
      items: ELEMENT_DATA
    };
    return of(o);
  }

  createOffer(o: any): Observable<any> {
    return this.http.post(this.url + "/v1/offer", o);
  }

  removeOffer(id: any): Observable<any> {
    const index = offers.findIndex(i => i.id === id);
    offers.splice(index, 1);
    return of("done");
  }

  getJobFields(): Observable<any> {
    return of(JSON.parse(localStorage.getItem("job")));
  }

  addJobFields(o: any): void {
    localStorage.setItem("job", JSON.stringify(o));
  }

  removeJobField(o: any): void {
    localStorage.setItem("job", JSON.stringify(o));
  }

  register(email: string, password: string): Observable<any> {
    return this.http.post(this.url + "/account/register", {
      email: email,
      password: password,
      confirmPassword: password
    });
  }

  addressSearch() {
    const a =
      "https://maps.googleapis.com/maps/api/geocode/xml?address=1600+Amphitheatre+Parkway,+Mountain+View,+CA&key=AIzaSyDgdcZQLAIbJlwa84JdjS1_DKI1MpbCPFs";
    return this.http.get(a);
  }
}
