import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormControl, AbstractControl, Validators } from '@angular/forms';

interface Type {
  value: string;
  viewValue: string;
}

function isValid(input: FormControl) {
  var re = new RegExp("^[a-z]+$");
  return input.value.match(re) ? { valid: true } : null;
}

@Component({
  selector: 'app-add-property',
  templateUrl: './add-property.component.html',
  styleUrls: ['./add-property.component.scss']
})
export class AddPropertyComponent {

  name = new FormControl('', [Validators.required, isValid]);

  types: Type[] = [
    {value: 'string', viewValue: 'string'},
    {value: 'number', viewValue: 'number'},
    {value: 'date', viewValue: 'date'}
  ];

  constructor(
    public dialogRef: MatDialogRef<any>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      this.name.setValue(data.name);
      this.name.valueChanges.subscribe(r => {
        data.name = r;
      })
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
