import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest
} from "@angular/common/http";
import { Injectable } from "@angular/core";
import { OAuthService, OAuthStorage } from "angular-oauth2-oidc";
import { Observable } from "rxjs";

@Injectable()
export class DefaultOAuthInterceptor implements HttpInterceptor {
  constructor(
    private oauthService: OAuthService,
    private authStorage: OAuthStorage //private errorHandler: OAuthResourceServerErrorHandler, //@Optional() private moduleConfig: OAuthModuleConfig
  ) {}

  private checkUrl(url: string): boolean {
    /*
      let found = this.moduleConfig.resourceServer.allowedUrls.find(u =>
        url.startsWith(u)
      );
      return !!found;
      */
    return true;
  }

  public intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    let url = req.url.toLowerCase();

    /*
      if (!this.moduleConfig) return next.handle(req);
      if (!this.moduleConfig.resourceServer) return next.handle(req);
      if (!this.moduleConfig.resourceServer.allowedUrls) return next.handle(req);
      if (!this.checkUrl(url)) return next.handle(req);
  
      let sendAccessToken = this.moduleConfig.resourceServer.sendAccessToken;
      */
    if (true) {
      let token = this.oauthService.getAccessToken(); //this.authStorage.getItem("access_token");
      let header = "Bearer " + token;

      let headers = req.headers.set("Authorization", header);

      req = req.clone({ headers });
    }

    return next.handle(req);
    //.catch(err => /*this.errorHandler.handleError(err)*/ null);
  }
}
