import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit } from "@angular/core";
import { MatDialog, MatTableDataSource } from "@angular/material";
import { ActivatedRoute, Router } from "@angular/router";
import { format } from "date-fns";
import { Observable } from "rxjs";
import Swal, { SweetAlertType } from "sweetalert2";
import { AddNewComponent } from "../add-new/add-new.component";
import { ApiService, OfferItem } from "../api.service";
import { OfferDetail } from "../shared/job.model";
import { offers } from "../shared/test-data";
import { SendComponent } from "./send/send.component";

@Component({
  selector: "app-view",
  templateUrl: "./view.component.html",
  styleUrls: ["./view.component.scss"]
})
export class ViewComponent implements OnInit {
  offer$: Observable<OfferDetail>;
  offer: OfferDetail;

  displayedColumns: string[] = [
    "select",
    "position",
    "name",
    "weight",
    "symbol",
    "reply"
  ];
  dataSource = new MatTableDataSource<OfferItem>();
  selection = new SelectionModel<OfferItem>(true, []);

  constructor(
    public dialog: MatDialog,
    private api: ApiService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe(p => {
      const id = Number(p["params"].id);

      this.offer$ = this.api.getOfferDetail(id);
      this.offer$.subscribe(o => {
        if (!o) {
          return;
        }
        this.offer = o;
        this.dataSource = new MatTableDataSource<OfferItem>(o.items);
      });
    });
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  hasSelection() {
    return this.selection.selected.length > 0;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected()
      ? this.selection.clear()
      : this.dataSource.data.forEach(row => this.selection.select(row));
  }

  createSubOffer() {
    const date = format(new Date(), "DD.MM.YYYY HH:mm");

    const dialogRef = this.dialog.open(AddNewComponent, {
      width: "250px",
      data: {
        created_at: date
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!result || (result && Object.keys(result).length === 0)) {
        return;
      }

      const o: OfferDetail = {
        id: offers.length,
        parent_id: this.offer.id,
        name: result.name,
        created_at: result.created_at,
        items: this.selection.selected
      };

      this.api.createOffer(o).subscribe(r => {
        this.selection.clear();
        this.router.navigate(["/new", r.id]);
      });
    });
  }

  send() {
    const dialogRef = this.dialog.open(SendComponent, {
      width: "300px",
      data: this.offer
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!result || (result && Object.keys(result).length === 0)) {
        return;
      }
      console.log(result);
    });
  }

  delete(o: any): void {
    const type: SweetAlertType = "warning";
    const options = {
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this offer!",
      type: type,
      showCancelButton: true
    };

    Swal.fire(options).then(willDelete => {
      if (willDelete.value) {
        this.api.removeOffer(o.id);
        this.router.navigate(["/new"]);
      }
    });
  }
}
